nav {
  float: left;
}

nav ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

nav li {
  display: inline-block;
  margin-left: 70px;
  padding-top: 15px;

  position: relative;
}

nav a {
  font-family: 'Raleway', sans-serif;
  color: #bfc9ca;
  text-decoration: none;
  text-transform: uppercase;
  font-weight: 600;
  font-size: 12px;
  letter-spacing: 0.5rem;
  padding-bottom: 10px;
}

nav a:hover {
  color: rgb(202, 202, 202);
}

nav a::before {
  content: '';
  display: block;
  height: 5px;
  background-color: rgb(226, 223, 223);

  position: absolute;
  top: 0;
  width: 0%;

  transition: all ease-in-out 250ms;
}

nav a:hover::before {
  width: 100%;
}
